// #############################
//   Scaffolding Mixins
// #############################

// #############################
//   Containers
// #############################

@mixin make-container(){
	display: block;

	width: 100%;
	max-width: $content-width;
	
	padding: 0 $side-padding;
}

// #############################
//   Rows
// #############################

@mixin make-row(){
	margin-left: -$side-padding;
	margin-right: -$side-padding;
	
	@if $use-flex == true {
		display: flex;
	} @else {
		&.flex {
			display: flex;
		}
	}
	
	@extend %clearfix;
}

// #############################
//   Boxes
// #############################

@mixin make-box(){
	display: block;
	padding: 0 $side-padding;

	flex-grow: 1;
	width: 100%;
	
	& > & {
		padding-left: 0;
		padding-right: 0;
	}
}

// #############################
//   Columns
// #############################

@function get-column-width($span: 1, $columns: 12){
    @return 100% / ($columns / $span);
}

@mixin make-column(){
	display: block;
	padding: 0 $side-padding;

	@if $use-flex == true {
		flex-grow: 0;
		flex-shrink: 0;
	} @else {
		position: relative;
		float: left;
	}
	
	& > & {
		padding-left: 0;
		padding-right: 0;
	}
}

@mixin column-span($span: 1, $columns: 12){
    $width: 100% / ($columns / $span);
}

@mixin make-column-offset($shift, $columns: 12, $size: sm, $class: col){
	.#{$class}.offset-#{$size}#{$shift} {
		margin-left: get-column-width($shift, $columns);
	}
}

@mixin make-column-push($push, $columns: 12, $size: sm, $class: col){
	.#{$class}.push-#{$size}#{$push} {
		left: get-column-width($push, $columns);
	}
}

@mixin make-column-pull($pull, $columns: 12, $size: sm, $class: col){
	.#{$class}.pull-#{$size}#{$pull} {
		right: get-column-width($pull, $columns);
	}
}

@mixin make-column-offsets-for-size($size, $columns: 12, $class: col){
    $col-widths:();
    
    @for $i from 1 through $columns {
        $offset: 100% / ($columns / $i);

        @if not index($col-widths, $offset) {
			@include make-column-offset($i, $columns, $size, $class)
            @include make-column-push($i, $columns, $size, $class)
			@include make-column-pull($i, $columns, $size, $class)


            $col-widths: append($col-widths, $offset, comma);
        }   
    }
}

@mixin make-columns-for-size($size, $columns: 12, $class: col) {
    $col-widths:();
    
    @for $i from 1 through $columns {
        @include column-span($i, $columns);
        $width: get-column-width($i, $columns);
        @if not index($col-widths, $width) {
            .#{$class}.#{$size}#{$i} {
                width: $width;
                margin-left: 0;
            }

            $col-widths: append($col-widths, $width, comma);
        }   
    }
}

@mixin make-columns($breakpoints: null, $columns: 12, $class: col, $offsets: true){
	@if $breakpoints == null {
		$breakpoints: ("w": "");
	}
	@for $i from 1 through length($breakpoints) {
        $pair:  nth($breakpoints, $i);
        $size:  nth($pair, 1);
        $media: nth($pair, 2);  
        
        @if ($media == '') {
            @include make-columns-for-size($size, $columns, $class);
			@if $offsets == true {
				@include make-column-offsets-for-size($size, $columns, $class); 
			}
            
        } 
        @if $media != '' {
            @include respond-to($size){
                @include make-columns-for-size($size, $columns, $class);
                @if $offsets == true {
					@include make-column-offsets-for-size($size, $columns, $class); 
				}
            }   
        }
    }
}


